<template>
    <div>
        <b-breadcrumb :items="
        [
            {
                text:' ',
                href:'/organization/list'
            },
            {
                text:' ',
                href:`/organization/list/${org_id}`
            }
        ]"/>
        <b-card no-body>
            <b-card-header header-tag="header" class="p-1" role="tab">
                <span> <b-icon icon="bookmark-fill" scale="1.5" class="mr-2 ml-1"></b-icon> </span>
            </b-card-header>
            <b-card-body>
                <b-table-simple bordered hover>
                    <b-thead>
                        <b-tr>
                            <b-th
                                >        ,
                                        
                                 ,. </b-th
                            >
                            <b-th
                                >       ,  ,.
                                </b-th
                            >
                            <b-th>    1 . . , </b-th>
                            <b-th>   , ..</b-th>
                        </b-tr>
                    </b-thead>
                    <b-tbody>
                        <b-tr>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ programm.finance_volume }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ programm.finance_events }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell cursor-pointer center-text-in-cell">
                                <label class="cursor-pointer">{{ programm.cost }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ waited_square }}</label>
                            </b-th>
                        </b-tr>
                    </b-tbody>
                </b-table-simple>
            </b-card-body>
        </b-card>

        <b-card no-body class="mt-4">
            <b-card-header header-tag="header" class="p-1" role="tab">
                <span>
                    <b-icon icon="gear-wide-connected" scale="1.5" class="mr-2 ml-1"></b-icon> 
                     
                </span>
            </b-card-header>
            <b-card-body class="hidden-overflow">
                <b-table-simple bordered hover>
                    <b-thead>
                        <b-tr>
                            <b-th></b-th>
                            <b-th></b-th>
                            <b-th></b-th>
                            <b-th> </b-th>
                            <b-th> ,  . </b-th>
                            <b-th></b-th>
                            <b-th> .  (.)</b-th>
                            <b-th> </b-th>
                            <b-th> </b-th>
                            <b-th> </b-th>
                            <b-th>  : ,   .. ( )</b-th>
                            <b-th>  </b-th>
                            <b-th>     . (. )</b-th>
                            <b-th>    (. )</b-th>
                            <b-th></b-th>
                        </b-tr>
                    </b-thead>
                    <b-tbody>
                        <b-tr v-for="(item, index) in items" :key="index" @click="goToObj(item.id)">
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ index + 1 }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.type == '1' ? '' : '' }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.id_priority }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell cursor-pointer center-text-in-cell">
                                <label class="cursor-pointer">{{ item.city }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.name }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.assignment }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.square_kap }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.address }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.year }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ getIznos(item.wear) }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.regulation }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer"></label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.finance_sum }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer">{{ item.coFinancing }}</label>
                            </b-th>
                            <b-th class="normal-font-weight-for-sell center-text-in-cell cursor-pointer">
                                <label class="cursor-pointer"></label>
                            </b-th>
                        </b-tr>
                    </b-tbody>
                </b-table-simple>

                <b-pagination v-show="currentPage" :per-page="perPage" v-model="currentPage" />
            </b-card-body>
        </b-card>
    </div>
</template>

<script>
import Axios from "axios";
import {
    BCard,
    BCardBody,
    BCardHeader, BBreadcrumb,
    BPagination, BTableSimple, BTbody, BTh, BThead, BTr
} from "bootstrap-vue";
export default {
    components:{
        BBreadcrumb,
        BCard,
        BCardHeader,
        BCardBody,
        BTableSimple,
        BThead,
        BTr,
        BTh,
        BTbody,
        BPagination,
    },
    data() {
        return {
            programm: {},
            currentPage: 1,
            perPage: 5,
            csrf: document.getElementsByName("csrf-token")[0].content,
            org_id: null,
            items: []
        };
    },
    async mounted() {
        this.org_id = this.$route.params.id;
        await this.getCurentOrg();
    },
    computed: {
        waited_square() {
            return (
                ((parseFloat(this.programm.finance_volume) - parseFloat(this.programm.finance_events)) * 1000) /
                parseFloat(this.programm.cost)
            ).toFixed(2);
        }
    },
    methods: {

        async getCurentOrg() {
            return Axios.post(`/api/org-table/${this.org_id}`, null, {
                headers: {
                    "X-CSRF-Token": this.csrf
                }
            }).then(res => {
                this.items = res.data.objects;
                this.programm = res.data.programm;
            });
        },
        getIznos(iznos) {
            let izn = parseInt(iznos);
            if (izn === 0) {
                return " 20%";
            } else if (izn === 1) {
                return " 20%  50%";
            } else if (izn === 2) {
                return " 50%  70%";
            } else if (izn === 3) {
                return " 70%  90%";
            } else if (izn === 4) {
                return " 90%";
            }
        },
        goToObj(id) {
            window.location = `/organization/obj/${id}`;
        }
    }
};
</script>

<style scoped>
.hidden-overflow {
    overflow: hidden;
    overflow-x: scroll;
}
.normal-font-weight-for-sell {
    font-weight: normal !important;
}
.cursor-pointer {
    cursor: pointer;
}
</style>
